export default {
	data() {
		return {
			email:'',
			psd:''
		}
	},
	computed:{
		isClick(){
			if(this.email.trim() && this.psd.trim()){
				return true;
			}
			return false;
		}
	},
	methods: {
		check(){
			const reg = /^[0-9a-zA-Z_.-]+[@][0-9a-zA-Z_.-]+([.][a-zA-Z]+){1,2}$/;
			if(reg.test(this.email)){
				return true;
			}else{
				return false
			}
		},
		updateEmail(){
			if(!this.check()){
				uni.showToast({
					icon:'none',
					title:'邮箱格式不对'
				})
			}else{
				console.log('ok')
			}
		}
	}
}
